.compose {
    position: absolute;
    bottom: 0;
    right: 70px;
    min-width: 300px !important;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
    box-shadow: 0px 0px 8px rgb(51 51 51/59%);
    background-color: #fff;
}

.compose .compose_header {
    display: flex;
    justify-content: space-between;
    background: #333;
    color: #fff;
    align-items: center;
    padding: 7px 15px;
}

.compose_header_left span {
    font-size: 14px;
}

.compose_header_right {
    display: flex;
    align-items: center;
}

.compose .compose_header .compose_header_right svg {
    font-size: 18px;
    margin-left: 10px;
    transition: 0.2s linear 0s;
    padding: 3px;
}

.compose_header_right svg:nth-child(2) path {
    transform: rotate(45deg);
    transform-origin: center;
}

.compose .compose_header .compose_header_right svg:hover {
    background: #545454;
    cursor: pointer;
}

.compose_body {
    display: flex;
}

.compose_body_form {
    display: flex;
    flex-direction: column;
}

.compose_body_form {
    width: 100% !important;
}

.compose_body_form input,
textarea {
    height: 40px;
    outline: 0;
    border: 0;
    box-shadow: inset 0 -1px 0 0 rgb(100 121 143/12%);
    padding: 0 10px;
}

textarea {
    height: auto;
    resize: none;
}

.compose_footer {
    display: flex;
    justify-content: space-between;
    padding: 12px 10px;
}

.compose_footer button {
    display: flex;
    align-items: center;
    cursor: pointer;
    border: 0;
    background-color: #297be6;
    outline: 0;
    color: #fff;
    font-size: 15px;
    padding: 0px 5px;
    padding-left: 15px !important;
    border-radius: 4px;
    letter-spacing: 1px;
}

.compose_footer button svg {
    border-left: 1px solid #00000052;
    margin-left: 15px;
    padding: 6px 2px 6px 2px;
}

.compose_footer_right svg {
    font-size: 19px;
    margin-left: 6px;
    border-radius: 3px;
    color: gray;
    padding: 3px;
    transition: 0.2 linear 0s;
    cursor: pointer;
}

.compose_footer_right svg:hover {
    background-color: #eee;
    color: #000;
}